<template>
    <html>
        <head>
            <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1.0, shrink-to-fit=no,user-scalable=0">
        </head>
        <body>
            <div id="discon">
                {{this.$route.params.username}}
                <input type="button" value="Request" @click="requestController()"/>
                <table style="border:1px;">
                <tr v-for="item in dataList">
                        <td>{{item.ulMarkUser}}</td>
                        <td>{{item.ulMobileNumber}}</td>
                        <td>{{item.ulSuccessTime}}</td>
                    </tr>
                </table>
                <mt-button @click="handleClick()">按钮</mt-button>
            </div>
        </body>
    </html>
</template>
<script>
import {Indicator} from 'mint-ui';
import axios from 'axios';
export default{
name: 'Test',
data () {
    return {
        dataList:[]
    }
},
mounted() {},
methods: {
    fn(){

    },
    requestController(){
        axios({
            method: 'get',
            url: 'main/test'
        }).then(response => {
            this.dataList = response.data;
        }).catch(error => {
            alert(error);
        })
    },
    handleClick(){
        Indicator.open();
    }
}
}
</script>
<style>
@import 'https://unpkg.com/mint-ui/lib/style.css';
</style>




